import './App.css';
import Header from './header/index'
import Footer from './footer/index'
import Home from './page/home/index'
import Login from './page/login/index'
import Profile from './page/profile/index'
import Settings from './page/settings/index'
import Create from './page/create/index'
// import Article from './page/article/index'
import React, { Component } from 'react'
import Cookies from 'js-cookie'
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom'


export default class App extends Component {
  render() {
    return (
      <div>
        <Router>
          <Header></Header>
          <Switch>
            <Route path='/home' component={Home}></Route>
            <Route path='/register' component={Cookies.get('cook') ? Home : Login}></Route>
            <Route path='/login' component={Cookies.get('cook') ? Home : Login}></Route>

            <Route path='/create' component={Cookies.get('cook') ? Create : Home}></Route>
            <Route path='/settings' component={Cookies.get('cook') ? Settings : Home}></Route>
            <Route path='/profile' component={Cookies.get('cook') ? Profile : Home}></Route>
            {/* <Route path='/article' component={Article}></Route> */}
            <Redirect to='/home'></Redirect>
          </Switch>
          <Footer></Footer>
        </Router>
      </div>
    )
  }
}

