// import axios from 'axios'
import React from 'react'
import Cookies from 'js-cookie'
import Pubsub from 'pubsub-js'
// import {obj} from '../../api/api'
let { obj } = require('../../api/api')


class A extends React.Component {
    unameipt(event) {
        Pubsub.publish('uname', event.target.value)
    }
    render() {
        return (
            <div>
                <fieldset className="form-group">
                    <input
                        className="form-control form-control-lg"
                        type="text"
                        placeholder="Your Name"
                        onBlur={this.unameipt}
                    />
                </fieldset>
            </div>)
    }
}

class Login extends React.Component {
    constructor() {
        super()
        this.state = {
            uname: '',
            emailipt: '',
            psdipt: ''
        }
        this.emailipt = this.emailipt.bind(this)
        this.psdipt = this.psdipt.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }
    componentDidMount() {
        Pubsub.subscribe('uname', (msg, data) => {
            this.setState({
                uname: data
            })
        })
    }
    isLogin() {
        return this.props.match.path === '/login'
    }
    emailipt(event) {
        this.setState({ emailipt: event.target.value })
    }
    psdipt(event) {
        this.setState({ psdipt: event.target.value })
    }
    onSubmit(event) {
        event.preventDefault()
        obj({
            method: 'post',
            url: this.state.uname ? '/api/users' : '/api/users/login',
            data: {
                "user": {
                    'username': this.state.uname ? this.state.uname : '',
                    "email": this.state.emailipt,
                    "password": this.state.psdipt
                }
            }
        }).then(res => {
            Cookies.set('cook', res.data.user.token)
            Pubsub.publish('token', res.data.user.token)
            this.props.history.push('/home')
        })
    }
    render() {
        return (
            <div>
                <div className="auth-page">
                    <div className="container page">
                        <div className="row">
                            <div className="col-md-6 offset-md-3 col-xs-12">
                                <h1 className="text-xs-center">
                                    {this.isLogin() ? 'Sign in' : 'Sign up'}
                                </h1>
                                <p className="text-xs-center">
                                    <a href="">
                                        {this.isLogin() ? 'Need an account?' : 'Have an account?'}
                                    </a>
                                </p>
                                <ul style={{ display: this.isLogin() ? 'none' : 'block' }} className="error-messages">
                                    <li>That email is already taken</li>
                                </ul>
                                <form onSubmit={this.onSubmit}>
                                    {this.isLogin() ? ' ' : <A />}
                                    <fieldset className="form-group">
                                        <input className="form-control form-control-lg" type="text" placeholder="Email" onBlur={this.emailipt} />
                                    </fieldset>
                                    <fieldset className="form-group">
                                        <input className="form-control form-control-lg" type="password" placeholder="Password" onBlur={this.psdipt} />
                                    </fieldset>
                                    <button className="btn btn-lg btn-primary pull-xs-right">
                                        {this.isLogin() ? 'Sign in' : 'Sign up'}
                                    </button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Login