import React from 'react'
import Cooikes from 'js-cookie'
let { obj } = require('../../api/api')
let { addFavorite, deleteFavorite } = require('../../api/favorite')
class Home extends React.Component {
    constructor() {
        super()
        this.state = {
            data: {
                articles: []
            },
            mainlist: [],
            rows: 10,
            activepage: 0,
            disabledbtn: false
        }
        this.toggle = this.toggle.bind(this)
        this.clickcont = this.clickcont.bind(this)
    }
    componentDidMount() {
        obj({
            method: 'get',
            url: '/api/articles',
            params: {
                limit: this.state.rows
            }
        }).then(res => {
            var mainlist = []
            for (let i = 0; i < res.data.articlesCount / this.state.rows; i++) {
                mainlist.push(i)
            }
            this.setState({
                mainlist: mainlist,
                data: res.data
            })
        })
    }
    toggle(index) {
        return (e) => {
            e.preventDefault()
            obj({
                method: 'get',
                url: '/api/articles',
                params: {
                    limit: this.state.rows,
                    offset: index * this.state.rows
                }
            }).then(res => {
                this.setState({
                    data: res.data,
                    activepage: index
                })
            })
        }
    }
    async clickcont(data, index) {
        this.setState({
            disabledbtn: true
        })
        if (data.favorited) {
            await deleteFavorite(data).then((res) => {
                var article = res.data.article
                var arr = this.state.data.articles.map((item, i) => {
                    if (index == i) {
                        return article
                    }
                    return item
                })
                this.setState({
                    data: {
                        articles: arr
                    }
                })
            })
        } else {
            await addFavorite(data).then((res) => {
                var article = res.data.article
                var arr = this.state.data.articles.map((item, i) => {
                    if (index == i) {
                        return article
                    }
                    return item
                })
                this.setState({
                    data: {
                        articles: arr
                    }
                })
            })
        }
        this.setState({
            disabledbtn: false
        })
    }
    render() {
        return (
            <div>
                <div className="home-page">

                    <div className="banner">
                        <div className="container">
                            <h1 className="logo-font">conduit</h1>
                            <p>A place to share your knowledge.</p>
                        </div>
                    </div>

                    <div className="container page">
                        <div className="row">

                            <div className="col-md-9">
                                <div className="feed-toggle">
                                    <ul className="nav nav-pills outline-active">
                                        <li className="nav-item" style={{ display: Cooikes.get('cook') ? 'blck' : 'none' }}>
                                            <a className="nav-link disabled" href="">Your Feed</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link active" href="">Global Feed</a>
                                        </li>
                                    </ul>
                                </div>

                                {
                                    this.state.data.articles.map((item, index) => {
                                        return (
                                            <div className="article-preview" key={index}>
                                                <div className="article-meta">
                                                    <a href="profile.html"><img src={item.author.image} /></a>
                                                    <div className="info">
                                                        <a href="" className="author">{item.author.username}</a>
                                                        <span className="date">{item.createdAt}</span>
                                                    </div>
                                                    <button className="btn btn-outline-primary btn-sm pull-xs-right" onClick={() => { this.clickcont(item, index) }} disabled={this.state.disabledbtn}>
                                                        <i className="ion-heart">{item.favoritesCount}</i>
                                                    </button>
                                                </div>
                                                <a href="" className="preview-link">
                                                    <h1>{item.title}</h1>
                                                    <p>{item.description}</p>
                                                    <span>Read more...</span>
                                                </a>
                                            </div>
                                        )
                                    })
                                }

                                <div className='bottom-page ng-isolate-scope'>
                                    <nav>
                                        <ul className='pagination'>
                                            {
                                                this.state.mainlist.map((item, index) => {
                                                    return <li key={index} className={this.state.activepage == index ? 'page-item ng-scope active' : 'page-item ng-scope'} onClick={this.toggle(index)}>
                                                        <a className="page-link ng-binding" href="">{item + 1}</a>
                                                    </li>
                                                })
                                            }
                                        </ul>
                                    </nav>
                                </div>
                            </div>

                            <div className="col-md-3">
                                <div className="sidebar">
                                    <p>Popular Tags</p>
                                    <div className="tag-list">
                                        <a href="" className="tag-pill tag-default">programming</a>
                                        <a href="" className="tag-pill tag-default">javascript</a>
                                        <a href="" className="tag-pill tag-default">emberjs</a>
                                        <a href="" className="tag-pill tag-default">angularjs</a>
                                        <a href="" className="tag-pill tag-default">react</a>
                                        <a href="" className="tag-pill tag-default">mean</a>
                                        <a href="" className="tag-pill tag-default">node</a>
                                        <a href="" className="tag-pill tag-default">rails</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default Home