import React from 'react'
import './index.css'
import { Link, NavLink } from 'react-router-dom'
import Cookies from 'js-cookie'
import Pubsub from 'pubsub-js'

class Header extends React.Component {
    constructor() {
        super()
        this.state = {
            msg: ''
        }
    }
    render() {
        // console.log(Cookies.get('cook') ,'cook');
        // render重新设置渲染数据，就不用手动刷新了，自动帮你刷新页面
        Pubsub.subscribe('token', (msg, data) => {
            this.setState({
                msg: data
            })
        })
        return (
            <div>
                <nav className="navbar navbar-light">
                    <div className="container">
                        <Link className="navbar-brand" to="/home">conduit</Link>
                        <ul className="nav navbar-nav pull-xs-right">
                            <li className="nav-item">
                                <NavLink className="nav-link " to="/home" activeClassName='active'>Home</NavLink>
                            </li>
                            <li className="nav-item" style={{ display: Cookies.get('cook') === undefined  ? 'block' : 'none' }}>
                                <NavLink className="nav-link" to="/login" activeClassName='active'>Sign in</NavLink>
                            </li>
                            <li className="nav-item" style={{ display: Cookies.get('cook') === undefined ? 'block' : 'none' }}>
                                <NavLink className="nav-link" to="/register" activeClassName='active'>Sign up</NavLink>
                            </li>

                            <li className="nav-item" style={{ display: Cookies.get('cook') === undefined ? 'none' : 'block' }}>
                                <NavLink className="nav-link" to="/create" activeClassName='active'>
                                    <i className="ion-compose"></i>&nbsp; New Article
                                 </NavLink>
                            </li>
                            <li className="nav-item" style={{ display: Cookies.get('cook') === undefined ? 'none' : 'block' }}>
                                <NavLink className="nav-link" to="/settings" activeClassName='active'>
                                    <i className="ion-gear-a"></i>&nbsp;  Settings
                                 </NavLink>
                            </li>
                            <li className="nav-item" style={{ display: Cookies.get('cook') === undefined ? 'none' : 'block' }}>
                                <NavLink className="nav-link itemimg" to="/profile" activeClassName='active'>
                                    <img src='https://z3.ax1x.com/2021/04/29/gkpZU1.jpg'></img>
                                    <span>shuai666666</span>
                                </NavLink>
                            </li>
                        </ul>
                    </div>
                </nav>
            </div>
        )
    }
}

export default Header