import { obj } from './api'
import Cookies from 'js-cookie'

export let addFavorite = (data) => {
    var slug = data.slug
    // 401没有访问权限，需要拦截
    obj.interceptors.request.use(
        function (config) {
            config.headers.Authorization =
                " Token " + Cookies.get("cook");
            return config;
        }, function (error) {
            return Promise.reject(error);
        }
    );
    return obj({
        method: 'POST',
        url: `/api/articles/${slug}/favorite`
    })
}
 export let deleteFavorite = (data) => {
    var slug = data.slug
    // 401没有访问权限，需要拦截
    obj.interceptors.request.use(
        function (config) {
            config.headers.Authorization =
                " Token " + Cookies.get("cook");
            return config;
        }, function (error) {
            return Promise.reject(error);
        }
    );
    return obj({
        method: 'DELETE',
        url:  `/api/articles/${slug}/favorite`
    })
}